/* START 404 */
body .no-found-container{
    align-content: center;
    align-items: center;
    border: none;
    bottom: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    height: 87%;
    justify-content: center;
    overflow-x: hidden;
    overflow-y: auto;
    right: 0;
    width: 85%;
    z-index: 100;
}

.title-404 {
    color: rgb(59, 59, 59);
    font-size: 27px;
    font-weight: 900;
    letter-spacing: 0.05em;
}

.icon-404 {
    width: 15rem;
}

.btn-outline-info {
    background: none;
    border: 2px solid #0191CE;
    border-color: #0191CE;
    color: #0191CE;
    font-size: 15px;
    font-weight: 600;
    letter-spacing: 0.05em;
    padding: 1rem 1rem;
}

.btn-outline-info:hover {
    cursor: pointer;
}
/* END 404 */

/* START SIDEBAR */
body .sidebar {
    background-color: #fff;
    border: 0;
    bottom: 0;
    box-shadow: inset -0.5px 0 0 #d3d3d3;
    display: block;
    left: 0;
    overflow: scroll;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 15%;
    z-index: 100;
}

button { 
    -webkit-tap-highlight-color: rgba(0,0,0,0); 
}

a { 
    -webkit-tap-highlight-color: rgba(0,0,0,0); 
}

.sidebar-logo {
    align-items: center;
    display: flex;
    height: 10%;
    justify-content: flex-start;
    margin: .6rem 0 0 1rem;
    width: 100%;
}

.sidebar-logo h1 {
    font-size: 25px;
    font-weight: 900;
    letter-spacing: 0.1em;
    line-height: 29px;
    padding-left: .5rem;
}

h2 {
    color: #919191;
    font-size: 14px;
    font-weight: 500;
    line-height: 15px;
    margin: 2rem 0 0 .95rem;
}

ul {
    list-style: none;
    margin: 1rem 0 0 .3rem;
    padding: 0;
}

ul li {
    display: flex;
    height: 1.5rem;
    justify-content: flex-start;
    margin: .3rem 0 0 0;
    padding: .7rem 1.5rem .7rem .6rem;
}

ul li:hover{
    background-color: #e7e7e8;
    border-radius: 5rem;
    width: min-content;
}

ul li .nav-link {
    align-items: center;
    color: #666666;
    display: flex;
    font-size: 17px;
    text-decoration: none;
}

.active {
    color: #222222;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
}

.active .circle-active {
    background-color: #0191CE;
    border-radius: 50%;
    height: .3rem;
    margin-left: .5rem;
    width: .3rem;
}

.icon-li-home,
.icon-li-client,
.icon-li-article,
.icon-li-invoice,
.icon-li {
    background-repeat: no-repeat;
    height: 23px;
    padding-right: .5rem;
    width: 25px;
}

.icon-li-home {
    background-image: url('/src/assets/homeIcon.svg');
}

.icon-li-client {
    background-image: url('/src/assets/clientIcon.svg');
}

.icon-li-article {
    background-image: url('/src/assets/stockIcon.svg');
}

.icon-li-invoice {
    background-image: url('/src/assets/invoiceIcon.svg');
}

.active .icon-li-home {
    background-image: url('/src/assets/homeIconFill.svg');
}

.active .icon-li-client {
    background-image: url('/src/assets/clientIconFill.svg');
}

.active .icon-li-article {
    background-image: url('/src/assets/stockIconFill.svg');
}

.active .icon-li-invoice {
    background-image: url('/src/assets/invoiceIconFill.svg');
}

.sidebar .logout {
    align-items: center;
    background: none;
    border: none;
    bottom: 0;
    color: #FF4646;
    display: flex;
    font-size: 20px;
    font-weight: 400;
    justify-content: center;
    line-height: 23px;
    margin-bottom: 2rem;
    position: absolute;
    text-decoration: none;
    width: 100%;
}

.sidebar .logout:hover {
    cursor: pointer;
}

.icon-li {
    height: 23px;
}
/* END SIDEBAR */



/* START NAVBAR */
body .navbar {
    background-color: #F9F9F9;
    display: block;
    height: 13%;
    min-height: 66px;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    right: 0;
    top: 0;
    width: 85%;
    z-index: 100;
}

.navbar-container {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: space-between;
    width: 100%;
}

.navbar-mobile {
    display: none;
}

.navbar-menu {
    cursor: pointer;
}

.navbar-logo {
    align-items: center;
    display: flex;
    justify-content: flex-start;
    margin-left: .7rem;
}

.navbar-logo img {
    height: 30px;
    width: 30px;
}

.navbar-logo h1 {
    font-size: 1.5em;
    font-weight: bold;
    margin-left: 0.1em;
}

.search-form{
    align-items: center;
    background: linear-gradient(135.37deg, rgba(0, 0, 0, 0.4) 4.29%, rgba(255, 255, 255, 0.4) 95.6%, rgba(255, 255, 255, 0.4) 95.6%, rgba(255, 255, 255, 0.4) 95.6%), #F9F9F9;
    background-blend-mode: soft-light, normal;
    border: 0.5px solid rgba(229, 222, 222, 0.4);
    border-radius: 40px;
    box-shadow: -5px -5px 10px #FFFFFF, 5px 5px 10px #D1D1D1;
    box-sizing: border-box;
    display: flex;
    height: 2.5rem;
    margin: 0 1rem;
    padding: .5rem .7rem;
}

.search-form input {
    background: none;
    border: none;
    height: 100%;
}

.search-form input:focus {
    outline: none;
}

.search-form button {
    background: none;
    border-bottom: none;
    border-left: 1.5px solid #81818166;
    border-right: none;
    border-top: none;
    height: 100%;
    padding-left: .7rem;
}

.search-form button:hover { cursor: pointer; }

.navbar-user {
    align-items: center;
    background: linear-gradient(135.37deg, rgba(0, 0, 0, 0.4) 4.29%, rgba(255, 255, 255, 0.4) 95.6%, rgba(255, 255, 255, 0.4) 95.6%, rgba(255, 255, 255, 0.4) 95.6%), #F9F9F9;
    background-blend-mode: soft-light, normal;
    border: 0.5px solid rgba(229, 222, 222, 0.4);
    border-radius: 40px;
    box-shadow: -5px -5px 10px #FFFFFF, 5px 5px 10px #CFCFCF;
    box-sizing: border-box;
    display: flex;
    height: 2.5rem;
    justify-content: space-between;
    margin: 0 1rem;
    padding: .5rem .7rem;
}

.navbar-user p {
    align-items: center;
    color: #666666;
    display: flex;
    font-size: 13px;
    font-weight: 700;
    height: 30px;
    justify-content: center;
    letter-spacing: 0.05em;
    margin: 0;
    margin-left: .5rem;
    overflow-y: scroll;
    text-align: center;
    width: 80px;
}
/* END NAVBAR */

@media only screen and (max-width: 1115px) {
    .sidebar .logout {
        font-size: 15px;
    }
}

@media only screen and (max-width: 1000px) {

    ul li {
        padding: .7rem 1rem .7rem .6rem;
    }

}

@media only screen and (max-width: 960px) {
    /* Ocultar SideBar */
    body .sidebar {
        position: fixed;
        left: -200px;
        min-width: 144px;
        transition: .5s;
    }

    body .active-sidebar {
        left: 0px;
        min-width: 144px;
        position: fixed;
        transition: .3s;
    }

    body .navbar {
        width: 100%;
        z-index: 850;
    }

    h2 {
        margin-top: 2rem;
    }

    .navbar-mobile {
        align-content: center;
        align-items: center;
        display: flex;
        margin-left: .7rem;
    }
    
}

@media only screen and (max-height: 400px) {
    .sidebar .logout { margin-bottom: .2rem; }
}

@media only screen and (max-height: 350px) {
    body .sidebar {
        min-height: 350px;
    }
}

@media only screen and (min-width: 1250px){
    
    .navbar-container {
        justify-content: space-between;
    }

    .search-form{
        margin-left: 10rem;
    }

    .search-form input {
        width: 35rem;
    }

    .navbar-user {
        margin-right: 2rem;
    }
}


@media only screen and (max-width: 1249px) and (min-width: 961px) {
    
    .navbar-container {
        justify-content: space-between;
    }

    .search-form{
        margin-left: 5rem;
    }

    .search-form input {
        width: 27rem;
    }

    .navbar-user {
        margin-right: 2rem;
    }
}

@media only screen and (max-width: 1049px) and (min-width: 740px) {
    
    .navbar-container {
        justify-content: space-between;
    }

    .search-form input {
        width: 20rem;
    }

    .navbar-user {
        margin-right: 2rem;
    }
}

@media only screen and (max-width: 739px) and (min-width: 640px) {

    .search-form input {
        width: 14rem;
    }
}

@media only screen and (max-width: 639px) {
    .navbar-mobile {
        margin-left: .3rem;
    }

    .navbar-logo {
        margin-left: .3rem;
    }

    .search-form {
        margin: 0;
        margin-left: 1rem;
    }

    .search-form input {
        width: 10rem;
    }

    .navbar-user {
        margin-right: 1rem;
    }

    .navbar-user p {
        font-size: 10px;
        width: 50px;
    }
}

@media only screen and (max-width: 510px) {

    .navbar-mobile {
        margin-left: .7rem;
    }

    .navbar-logo {
        margin-left: .7rem;
    }

    .search-form {
        margin-right: 1.5rem;
    }

    .navbar-user {
        display: none;
    }

    .search-form input {
        width: 13rem;
    }

}

@media only screen and (max-width: 440px) {

    .search-form {
        margin-right: 1rem;
    }

    .search-form input {
        width: 10rem;
    }

}

@media only screen and (max-width: 390px) {

    .navbar-mobile {
        margin-left: .2rem;
    }

    .navbar-logo {
        margin-left: 0;
    }

    .navbar-logo h1 {
        font-size: 1.3em;
    }

    .search-form {
        padding: 0.5rem 0.5rem;
    }

    .search-form input {
        width: 8rem;
    }

    .navbar-user {
        display: none;
    }

}