body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

.main-container {
    background: linear-gradient(90deg, #0191CE 0%, #0191CE 45.83%, #FFFFFF 45.84%, #FFFFFF 100%);
    height: 100vh;
    margin: 0;
    position: relative;
    padding: 0;
    width: 100vw;
}

.card-form {
    align-content: center;
    background: #FFFFFF;
    border-radius: 10px;
    box-shadow: 0 4px 40px 10px rgba(0, 0, 0, 0.3);
    height: 35rem;
    left: 50%;
    margin: -17.5rem 0 0 -40%; 
    position: absolute;
    top: 50%;
    width: 80%;
}

.card-form .icon-credentials,
.card-form .icon-login {
    bottom: 0;
    height: 15rem;
    margin-left: 25%;
    position:absolute;
    width: 335.812px;
    z-index: 1;
}

.card-form .icon-login {
    margin-left: 30%;
}

.card-form .form-presentation {
    background: #0191CE;
    border-radius: 10px 0 0 10px;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 44.8%;
}

.form-presentation .information {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 7rem;
}

.form-presentation .info-login {
    margin-top: 10rem;
}

.form-logo {
    height: 5rem;
    padding-bottom: 1rem;
    width: 5rem;
}

.form-title-presentation {
    color: #FFFFFF;
    font-size: 3rem;
    font-weight: 900;
    margin: 0;
}

.card-form .form-container {    
    align-items: center;
    border-radius: 0 10px 10px 0;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    right: 0;
    position: absolute;
    top: 0;
    width: 55.2%;
}

.title-form,
.title-form-login {
    color: #0191CE;
    font-size: 2rem;
    font-weight: 600;
    margin: 0;
    padding-bottom: 1.5rem;
}

.title-form-login {
    padding-bottom: 2.5rem;
}

.title-input {
    color: #000000;
    font-size: 1.1rem;
    font-weight: 600;
    margin: 0;
}

.input-icono-user {
    background-image: url('/src/assets/userIcon.svg');
}

.input-icono-pass {
    background-image: url('/src/assets/passIcon.svg');
}

.input-icono-db {
    background-image: url('/src/assets/dbIcon.svg');
}

.input-icono-user,
.input-icono-pass,
.input-icono-db {
    align-items: center;
    background-position: .3rem center;
    background-repeat: no-repeat;
    background-size: 1.5rem;
    border-bottom: .1rem solid #A7A7A7;
    border-left: none;
    border-right: none;
    border-top: none;
    display: flex;
    height: 1.7rem;
    margin-bottom: 1.7rem;
    padding-left: 2rem;
    padding-top: .5rem;
    width: 15rem;
}

.input-icono-user input, 
.input-icono-pass input, 
.input-icono-db input {
    background: none;
    border: none;
    font-size: 1.3rem;
    font-weight: 200;
    width: 100%;
}

.input-icono-user input:focus,
.input-icono-pass input:focus,
.input-icono-db input:focus {
    outline: none;
}

.active-user{
    background-image: url('/src/assets/userIconFill.svg');
}

.active-pass{
    background-image: url('/src/assets/passIconFill.svg');
}

.active-db{
    background-image: url('/src/assets/dbIconFill.svg');
}

.active-user,
.active-pass, 
.active-db {
    align-items: center;
    background-position: .3rem center;
    background-repeat: no-repeat;
    background-size: 1.5rem;
    border-bottom: .1rem solid #0191CE;
    border-left: none;
    border-right: none;
    border-top: none;
    display: flex;
    height: 1.7rem;
    margin-bottom: 1.7rem;
    padding-left: 2rem;
    padding-top: .5rem;
    transition: .4s;
    width: 15rem;
}

.container-button {
    align-content: center;    
    align-items: center;
    display: flex;
    justify-content: center;
}

.button-form {
    background: #0191CE;
    border: none;
    border-radius: 5rem;
    box-shadow: 3px 4px 4px rgba(1, 145, 206, 0.5);
    color: #FFFFFF;
    font-size: 1.3rem;
    font-weight: 700;
    height: 4rem;
    text-align: center;
    width: 100%;
}

.button-form-block {
    background: #003c55;
    border: none;
    border-radius: 5rem;
    box-shadow: 3px 4px 4px rgba(1, 145, 206, 0.5);
    color: #FFFFFF;
    cursor: progress;
    font-size: 1.3rem;
    font-weight: 700;
    height: 4rem;
    text-align: center;
    width: 100%;
}

.button-form:hover {
    background: #0175a7;
    box-shadow: 3px 4px 4px rgba(0, 64, 92, 0.5);
    color: #d8d8d8;
    cursor: pointer;
}

@media only screen and (min-width: 1700px) {
    .card-form .icon-credentials {
        margin-left: 35%;
    }

    .card-form .icon-login {
        margin-left: 35%;
    }

}

@media only screen and (max-width: 1300px) {
    .card-form .icon-credentials {
        margin-left: 19%;
    }

    .card-form .icon-login {
        margin-left: 27%;
    }

}

@media only screen and (max-width: 1160px) {
    .card-form .icon-credentials {
        margin-left: 15%;
    }

    .card-form .icon-login {
        margin-left: 23%;
    }

}

@media only screen and (max-width: 1070px) {
    .card-form .icon-credentials {
        margin-left: 12%;
    }

    .card-form .icon-login {
        height: 230px;
        margin-left: 26%;
        width: 250px;
    }

}

@media only screen and (max-width: 990px) {
    .card-form .icon-credentials,
    .card-form .icon-login {
        display: none;
    }

    .form-presentation {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .form-presentation .information {
        margin: 0;
    }

    .form-title-presentation {
        font-size: 2.3rem;
        letter-spacing: .2rem;
    }

}

@media only screen and (max-width: 860px) {
    .form-title-presentation {
        font-size: 2rem;
    }

    .title-form {
        font-size: 1.7rem;
    }

}

@media only screen and (max-width: 780px) {
    .main-container {
        background: #0191CE;
    }

    .form-presentation {
        display: none;
    }

    .card-form {
        margin: -17.5rem 0 0 -32.5%;
        width: 65%;
    }

    .card-form .form-container {
        width: 100%;
    }

}

@media only screen and (min-width: 781px) and (max-height: 600px) {
    .card-form {
        height: 30rem;
        margin: -15rem 0 0 -40%;
    }

    .card-form .icon-credentials {
        height: 10rem;
        width: 22.5rem;
    }

}

@media only screen and (max-width: 780px) and (max-height: 600px) {

    .card-form {
        height: 30rem;
        margin: -15rem 0 0 -32.5%;
        width: 65%;
    }

}

@media only screen and (max-width: 540px) {

    .card-form {
        margin-left: -43.5%;
        margin-right: -43.5%;
        width: 87%;
    }

}

@media only screen and (max-width: 350px) {

    .main-container {
        min-width: 350px;
    }

    .card-form {
        min-width: 303px;
    }

}